import { useContext } from 'react';
import { LangContext } from '../../context/LangContext';
import './Contenu.css';
import dataApp from '../../assets/data';

const Contenu = () => {

    const {appLanguage} = useContext(LangContext);

    return (
        <div className="content">
            <h1 className="title">{dataApp[appLanguage].title}</h1>
            <p className="content-txt">{dataApp[appLanguage].txt}</p>
        </div>
    );
};

export default Contenu;