import React, { useContext } from 'react';
import './ToggleLangs.css';
import franceFlag from '../../assets/france.svg';
import spainFlag from '../../assets/spain.svg';
import unitedKingdomFlag from '../../assets/united-kingdom.svg';
import koreaFlag from '../../assets/south-korea.svg';
import { LangContext } from '../../context/LangContext';



const ToggleLangs = () => {

    const {switchAppLanguage} = useContext(LangContext);

    return (
        <div className='container-langs'>
            <img onClick={() => switchAppLanguage('FR')} src={franceFlag} alt="Drapeau Français" />
            <img onClick={() => switchAppLanguage('ES')} src={spainFlag} alt="Drapeau Espagnol" />
            <img onClick={() => switchAppLanguage('EN')} src={unitedKingdomFlag} alt="Drapeau Anglais" />
            <img onClick={() => switchAppLanguage('KR')} src={koreaFlag} alt="Drapeau Coréen" />
        </div>
    );
};

export default ToggleLangs;