import { createContext, useState } from "react";

export const LangContext = createContext();

const supportedLang = ['EN', 'FR', 'ES', 'KR'];
let browsLang = navigator.language.slice(0,2).toUpperCase();

if(supportedLang.indexOf(browsLang) === -1){
    console.log("Browser langage not Supported");
    browsLang = "EN";
}

const LangContextProvider = props => {

    const [appLanguage, setAppLanguage] = useState(browsLang);

    const switchAppLanguage = (country) => {
        setAppLanguage(country);
    }

    return (
        <LangContext.Provider value={{appLanguage, switchAppLanguage}}>
            {props.children}
        </LangContext.Provider>
    );
};

export default LangContextProvider;